.post-container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin-top: 5rem;
}
.post-container .left .text{
    margin-top: 1rem;
}
.post-container .left .text h1{
    font-weight: 500;
    text-align: left;
    font-size: 3.125rem;
    width: 27rem;
}
.post-container .left .text h3{
    text-align: left;
    font-weight: 400;
    width: 25rem;
    font-size: 1.625rem;
}
.post-container .right img{
    height: 18rem;
}