.row {
  white-space: nowrap;
  color: #fff;
  padding: 1rem;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
.row h2 {
  font-size: 20px;
  font-weight: lighter;
}
.poster-container {
  overflow-x: scroll;
}
.poster-container::-webkit-scrollbar {
  display: none;
}
.poster {
  width: 12rem;
  height: 15rem;
  margin-right: 1rem;
  cursor: pointer;
  transition: 0.3s all;
  border-radius: 6px;
}
.smallPoster {
  width: 6rem;
  height: 10rem;
  margin-right: 1rem;
  cursor: pointer;
  transition: 0.3s all;
  border-radius: 6px;
}
.poster:hover {
  transform: scale(1.1);
}
.smallPoster:hover {
  transform: scale(1.1);
}
