.Banner {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
  width: 98%;
  margin-top: 0px;
  background-size: cover;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: #fff;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 10px;
  margin-top: 1rem;
}
.Banner .buttons button {
  padding: 0.5rem;
  border: none;
  outline: none;
  background-color: rgba(51, 51, 51, 0.5);
  cursor: pointer;
  color: #fff;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  padding-left: 2rem;
  padding-right: 2rem;
  font-weight: 500;
  letter-spacing: 1px;
  border-radius: 5px;
  font-weight: bold;
}
.Banner .description h1 {
  margin-top: 2rem;
  width: 15rem;
  font-size: 20px;
  font-weight: 500;
}
.Banner .description p {
  width: 18rem;
  color: #f2f2f2;
  font-size: 1rem;
  font-weight: 500;
  max-width: 307px;
}
.ml-5 {
  margin-left: 0.5rem;
}
.Banner .buttons button:hover {
  background-color: #fff;
  color: #111;
}
.fade {
  height: 7.4rem;
  background-image: linear-gradient(180deg, transparent, #030b17, #030b17);
  width: 100%;
  border-radius: 0 0 10px 10px;
}
.banner-content,
.buttons,
.description {
  margin-left: 2rem;
  padding-top: 3rem;
}
.banner-content h1 {
  position: relative;
  margin: 0px;
}
