.landing-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #fff;
  letter-spacing: 1px;
  text-align: center;
  width: 100vw;
}
.hero {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(https://assets.nflxext.com/ffe/siteui/vlv3/c0a32732-b033-43b3-be2a-8fee037a6146/2fe6e3c0-5613-4625-a0c1-3d605effd10b/IN-en-20210607-popsignuptwoweeks-perspective_alpha_website_medium.jpg);
}
.hero-content .text h1 {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 3.2rem;
  font-weight: 500;
}
.email-input {
  padding: 16px;
  font-size: medium;
  width: 20rem;
  font-weight: bold;
  outline: none;
}
.email-input::placeholder {
  font-size: 1.5rem;
  font-weight: lighter;
  color: #7e7d7d;

}
.submit {
  padding: 18px;
  font-size: medium;
  border: none;
  font-weight: bold;
  background-color: #e50914;
  color: #fff;
  outline: none;
  cursor: pointer;
}
@media screen and (min-width: 1000px) {
  .email-input {
    width: 25rem;
  }
}
